/* importing fonts */

/* about us section container */
.AboutUs{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 80px;
    /* flex-wrap: wrap; */
    position: relative;
    left: -7%;
}

/* about us img container */
.img-container-about-us{
    display: flex;
    justify-content: center;
    align-items: center;
}

/* red box styles */
.red-box{
    width: 200px;
    height: 144px;
    background: rgba(245, 71, 72, 0.25);
    position: relative;
    top: -130px;
    right: 170px;
    z-index: -1;
}

/* yellow box styles */
.yellow-box{
    width: 210px;
    height: 154px;
    background: rgba(251, 147, 0, 0.25);
    position: relative;
    bottom: -120px;
    left: 180px;
    z-index: -1;
}

/* about us text section styles */
.about-us-text-section{
    flex: 1 1;
}

/* about us text styles */
.about-us-text{
    font-family: Open Sans;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    text-align: justify;
    color: #000000;
    margin-bottom: 30px;
    width: 470px;
}

/* about us title styles */
.about-us-title{
    font-family: Open Sans;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    color: #FB9300;
}

/* about us heading styles */
.heading-about-us{
    font-family: Source Sans Pro;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;
    color: #343F56;
    margin-top: 3px;
    margin-bottom: 19px;
}

/* about us button styles */
.about-us-btn{
    width: 219px;
    height: 58px;
    background: #FF5C00;
    border-radius: 10px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    color: #FFFFFF;
    border: none;
    outline: none;
    cursor: pointer;
}

/* about us text section styles */
.about-us-text-section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 1;
}

