}

/* features bottom container styles */
.features-bottom{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 80px;
    flex-wrap: wrap;
}

/* features card container styles */
.features-card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
}

/* features card title styles */
.features-card-title{
    height: 27px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    color: #FF5C00;
    margin-top: 17px;
    margin-bottom: 27px;
}

/* features card text styles */
.features-card-text{
    width: 348px;
    height: 140px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    text-align: justify;
    color: #000000;
}

/* center card styles */
.center-card{
    width: 400px;
    height: 388px;
    background: #FFFFFF;
    box-shadow: 0px 0px 10px 7px rgba(179, 179, 179, 0.25);
    border-radius: 6px;
    padding: 10px;
}

/* first card styles */
.first-card{
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}

/* last card styles */
.last-card{
    -webkit-transform: translateY(60px);
            transform: translateY(60px);
}

/* responsivity */
@media screen and (max-width: 925px) {
    /* first card styles */
    .first-card{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        margin-bottom: 15px;
    }

    .last-card{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        margin-top: 15px;
    }
}
